import whCaptcha from '../../components/wh-captcha/wh-captcha.vue';
import uniSteps from '@/components/uni-steps/uni-steps.vue'

var isLoding = false;
var timer = null;
var timer_ = null;
//邮箱验证
var util = require('../../common/util.js');
var email; //输入的email值
var captcha; //输入的captcha值
export default {
	components: {
		whCaptcha,
		uniSteps
	},
	data() {
		return {
			agreement: false, // 是否同意用户协议
			auth: false,

			swiperLeft: 0, // 块滑动
			swiperLineWidth: 0, // 进度条滑动
			captchaLength: 6, // 验证码长度

			active: 0,
			activeList: [{
				title: '微信绑定'
			}, {
				title: '邮箱认证'
			}, {
				title: '登陆'
			}],

			userInfo: '',
		};
	},

	onBackPress(e) {
		// return true 表示禁止默认返回
		console.log("监听到返回")
		return false
	},

	methods: {
		changeAgreement() {
			this.agreement = !this.agreement;
		},

		changeAuth() {
			this.auth = !this.auth;
		},

		cancleAuth() {
			this.auth = false;
			this.lastStep(false);
		},

		/**
		 * 微信小程序登陆
		 */
		getUserInfo(e) {
			// console.log("getting UserInfo...")
			// console.log(e);

			// 加锁
			if (isLoding) {
				return;
			}
			isLoding = true;

			uni.showLoading({
				title: '载入中...',
			});
			setTimeout(() => {
				if (isLoding) {
					isLoding = false // 解锁
					uni.hideLoading();
					uni.showToast({
						title: "网络未知错误",
						icon: "none",
						duration: 1000
					})
				}
			}, 5000); // 延时5s timeout

			var info = e.detail;
			var that = this;
			uni.login({
				success: res_login => {
					// console.log('-------res_login，获取code-------')
					// console.log(res_login);
					// 后端获取openid 并设置用户信息
					uni.request({
						url: that.$serverUrl + '/user/getWxUserInfo',
						method: "POST",
						data: {
							encryptedData: info.encryptedData,
							iv: info.iv,
							code: res_login.code,

							nickname: info.userInfo.nickName,
							faceImg: info.userInfo.avatarUrl
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						success: (res) => {
							// console.log(res)
							if (res.data.status == 200) {
								console.log("暂存用户信息");
								that.userInfo = res.data.data;
								console.log(this.userInfo);

								isLoding = false;
								uni.hideLoading();

								// 下一步
								that.nextStep(true);
							}
						}
					});
				}, // end of login success
				fail: (res) => {
					isLoding = false;
					uni.hideLoading();
					uni.showToast({
						title: "login fail",
						icon: "none",
						duration: 1000
					})
				}

			});
		},

		onEmailInput(event) {
			email = event.target.value;
		},

		onCaptchaInput(event) {
			captcha = event.target.value;
		},

		/**
		 * true: 不匹配, false: 匹配
		 */
		checkUNNCEmail(str) {
			return !RegExp(/^\w+([-+.]\w+)*@nottingham\.edu\.cn+$/).test(str);
		},

		getCaptcha() {
			if (email) {
				// 检测邮箱
				if (util.regEmail(email) || this.checkUNNCEmail(email)) {
					uni.showToast({
						title: '非 UNNC 邮箱地址！',
						icon: 'none'
					});
				} else {
					if (this.$refs.captcha.canSend()) {
						console.log("获取验证码 email=" + email);
						this.$refs.captcha.begin();
						this.title = "重新发送"

						uni.request({
							url: this.$serverUrl + '/user/getCode',
							method: "POST",
							data: {
								userId: this.userInfo.id,
								email: email,
							},
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: (res) => {
								console.log(res);
							},
						});
					}
				}
			} else {
				uni.showToast({
					title: 'Email不能为空',
					icon: 'none'
				});
			}
		},

		confirmCode() {
			if (captcha) {
				uni.showLoading({
					title: "请等待"
				})
				uni.request({
					url: this.$serverUrl + '/user/confirmCode',
					method: "POST",
					data: {
						userId: this.userInfo.id,
						code: captcha,
						email: email,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.status == 200) {
							// 3.获取返回的用户信息
							var finalUser = res.data.data;
							// 4.分割邮箱地址, 重构 user
							this.userInfo = this.myUser(finalUser);
							console.log(this.userInfo);

							uni.showToast({
								title: '认证成功',
								icon: 'none'
							});
							this.changeAuth();
							this.nextStep(false);

						} else {
							console.log("验证失败 " + res.data.msg);
							uni.showToast({
								title: '验证失败',
								icon: 'none'
							});
						}
					},
					complete: () => {
						uni.hideLoading();
					}
				});
			} else {
				uni.showToast({
					title: '验证码为空',
					icon: 'none'
				});
			}
		},

		login() {
			this.nextStep(false);
			uni.showLoading({
				title: "正在登陆..."
			});

			// 5.写入缓存
			this.setGlobalUserInfo(this.userInfo);

			setTimeout(() => {
				uni.hideLoading();
				uni.redirectTo({
					url: "../index/index"
				});
			}, 1000)
		},

		/**
		 * 转场动画,需计算转场时间以保证进度条和块内容运动一致，
		 * 当前运动时间为 500 ms
		 * TODO: 暂未动态设置块移动距离,和同步运动时间 -Jerrio
		 * @param {Object} sync 是否控制“块”同步运动
		 */
		nextStep(sync) {
			this.active++;
			var point = 100.00 / this.activeList.length * this.active; //计算进度条节点 单位%
			// console.log(that.swiperLineWidth);
			// 进度条右滑
			clearInterval(timer_); //清空定时器，防止重复操作
			var that = this;
			timer_ = setInterval(function() {
				if (that.swiperLineWidth >= point) {
					clearInterval(timer_);
				} else {
					that.swiperLineWidth = that.swiperLineWidth + 2;
					// console.log(that.swiperLineWidth);
				}
			}, 20)

			if (sync) {
				// 块右滑
				clearInterval(timer); //清空定时器，防止重复操作
				var that = this;
				timer = setInterval(function() {
					if (that.swiperLeft <= -100) {
						clearInterval(timer);
					} else {
						that.swiperLeft = that.swiperLeft - 2;
						// console.log(that.swiperLeft);
					}
				}, 10)
			}
		},

		lastStep(sync) {
			this.active--;
			var point = 100.00 / this.activeList.length * this.active; //计算进度条节点 单位%
			// 进度条左滑
			clearInterval(timer_); //清空定时器，防止重复操作
			var that = this;
			timer_ = setInterval(function() {
				if (that.swiperLineWidth <= point) {
					clearInterval(timer_);
				} else {
					that.swiperLineWidth = that.swiperLineWidth - 2;
					// console.log(that.swiperLineWidth);
				}
			}, 20)

			if (sync) {
				// 块左滑
				clearInterval(timer); //清空定时器，防止重复操作
				var that = this;
				timer = setInterval(function() {
					if (that.swiperLeft >= 0) {
						clearInterval(timer);
					} else {
						that.swiperLeft = that.swiperLeft + 2;
						// console.log(that.swiperLeft);
					}
				}, 10)
			}
		},

		toUserDeal() {
			uni.navigateTo({
				url: "../userDeal/userDeal"
			});
		}
	}
};
